.ng2-c-header {
  background-image: linear-gradient(to right, #28648a, #004160);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: 50px;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 2; }

.ng2-c-header--sticky {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  transform: translate3d(0, 0, 0);
  visibility: visible;
  z-index: 51; }

.ng2-c-header__sidebar,
.ng2-c-header__main {
  position: relative;
  transition-duration: 0.5s;
  transition-property: transform;
  transition-timing-function: ease-in-out; }

.ng2-c-header__sidebar {
  min-width: 100%;
  transform: translateX(0);
  z-index: 1; }
  @media (min-width: 768px) {
    .ng2-c-header__sidebar {
      min-width: 280px;
      width: 280px; } }

@media (min-width: 768px) {
  .ng2-c-header__sidebar--wide {
    min-width: 320px;
    width: 320px; } }

.ng2-c-header__main {
  flex: 1;
  min-width: 100%;
  transform: translateX(0);
  z-index: 1; }
  @media (min-width: 768px) {
    .ng2-c-header__main {
      min-width: 0; } }

@media (max-width: 767px) {
  .ng2-c-view--sidebar-closed .ng2-c-header__sidebar,
  .ng2-c-view--sidebar-closed .ng2-c-header__main {
    transform: translateX(-100%); } }

.ng2-c-header__left,
.ng2-c-header__right {
  align-items: center;
  bottom: 0;
  display: flex;
  left: 0;
  position: absolute;
  top: 0;
  z-index: 3; }

.ng2-c-header__right {
  left: auto;
  padding-right: 10px;
  right: 0; }

.ng2-c-header__center {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  height: 50px;
  justify-content: center;
  margin: 0 50px;
  text-align: center; }

.ng2-c-header__center--no-margin {
  margin: 0; }

.ng2-c-header__center--md-margin {
  margin: 0 100px; }

.ng2-c-header__center--lg-margin {
  margin: 0 200px; }

.ng2-c-header__title {
  color: #ffffff;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-overflow: ellipsis;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
  word-wrap: normal;
  z-index: 2; }
  .ng2-c-header__title .icon-arrow-down {
    font-size: 8px; }

.ng2-c-header__subtitle {
  color: #ffffff;
  display: inline-block;
  font-size: 12px;
  max-width: 100%;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal; }

.ng2-c-header__icon {
  cursor: pointer;
  overflow: visible;
  position: relative;
  align-items: center;
  color: #ffffff;
  display: flex;
  font-size: 17px;
  line-height: 20px;
  padding: 0 10px; }
  .ng2-c-header__icon::after {
    background: transparent;
    content: '';
    display: block;
    height: 50px;
    left: 50%;
    min-width: 44px;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%); }

.ng2-c-header__icon-text {
  display: none; }
  @media (min-width: 768px) {
    .ng2-c-header__icon-text {
      display: inline-block;
      padding: 0 10px; } }
